<script>
import textComponents from './../components/text_components'
export default {
  name: 'collect_item',
  extends: textComponents,
  data () {
    return {
      activeItems: [], // 列表展示数据集合
      allData: [], // 总数据集合
      onLine: navigator.onLine,
      specialCode: '', // 转型类型
      practiceStatus: '',
      testIndex: 1 // 当前页号
    }
  },
  async created () {
    this.specialCode = this.$route.params.specialCode
    this.practiceStatus = this.$route.params.practiceStatus
    this.testIndex = this.$route.params.numberMes === '0' ? 1 : Number(this.$route.params.numberMes)
    await this.getDataList('findCollectExeEntity') // 调用基页面
    if (this.testIndex === 1) {
      this.initSwiper(0)
    } else if (this.testIndex === this.allData.length) {
      this.initSwiper(2)
    } else {
      this.initSwiper(1)
    }

    await this.getActiveItem(this.testIndex)
  }
}
</script>



